<template>
  <v-container
      
      class="grey lighten-5 mb-6"
    >
      <v-row
        style="height: 150px;"
     
     
      >
        <v-col v-for="item in exerciselist">
            <v-card class="pa-2" outlined tile @click="enterpart(item)">
               <v-img
               :src="item.src"
               class="white--text align-end"
               gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
               height="200px"
               >
                  <v-card-title > {{item.partname}}</v-card-title>
               </v-img>
               
            </v-card>
        </v-col>
      </v-row>
   </v-container>
</template>

<script>
export default {
 name:'ExerciseList',
 methods: {
    enterpart(a){
       this.$router.push({
          name:'part',
          params:{data:JSON.stringify(a)},
          
         
       })
    }
 },
 computed:{

   exerciselist(){
      return this.$store.state.exercise.exerciselist
   }
 },
 mounted() {
  
 },
}
</script>

